import React from "react";
import { useSelector } from "react-redux";
import { IStateType, IProductState } from "../../store/models/root.interface";
import { IProduct } from "../../store/models/product.interface";
import { useHistory } from "react-router-dom";

export type productListProps = {
  onSelect?: (product: IProduct) => void;
  children?: React.ReactNode;
};

const data = [
  {
    'id': "",
    'lesson': 'Bui 1',
    'teacher': 'Chung Nguyn',
    'number_stuent': '5/6',
  },
  {
    'id': "",
    'lesson': 'Bui 2',
    'teacher': 'Chung Nguyn',
    'number_stuent': '6/6',
  },
  {
    'id': "",
    'lesson': 'Bui 3',
    'teacher': 'Chung Nguyn',
    'number_stuent': '5/6',
  },
  {
    'id': "",
    'lesson': 'Bui 4',
    'teacher': 'Chung Nguyn',
    'number_stuent': '5/6',
  },
  {
    'id': '',
    'lesson': 'Bui 5',
    'teacher': 'Chung Nguyn',
    'number_stuent': '5/6',
  },
]

function LessonList(props: productListProps): JSX.Element  {

  const products: IProductState = useSelector((state: IStateType) => state.products);
  const history = useHistory();
  
  const routeChange = () =>{ 
    let path = '/class/lesson'; 
    history.push(path);
  }


  const productElements: (JSX.Element | null)[] = data.map(product => {
    if (!product) { return null; }
    return (<tr className={`table-row ${(products.selectedProduct && products.selectedProduct.id === product.id) ? "selected" : ""}`}
      key={`product_${product.id}`} onClick={routeChange}>
      <th scope="row">{product.id}</th>
      <td>{product.lesson}</td>
      <td>{product.teacher}</td>
      <td>{product.number_stuent}</td>
    </tr>);
  });


  return (
    <div className="table-responsive portlet">
      <table className="table">
        <thead className="thead-light">
          <tr>
            <th scope="col">#</th>
            <th scope="col">Bui</th>
            <th scope="col">Gio vin dy</th>
            <th scope="col">S hc sinh tham gia</th>
          </tr>
        </thead>
        <tbody>
          {productElements}
        </tbody>
      </table>
    </div>

  );
}

export default LessonList;
